
export class AdaptiveBlock {
    constructor(div) {
        this.adaptive = false;

        this.div = div;
        this.parent = this.div.parentElement;
        this.toDiv = document.querySelector(this.div.dataset.to);
        this.pos = this.div.dataset.pos;
        this.maxWidth = Number(this.div.dataset.width); 

        this.resize()

    }
    resize() {
        console.log(this.maxWidth)
        if(this.maxWidth >= window.innerWidth) {
            if(!this.adaptive) {
                this.changePosDiv(this.toDiv, this.pos, this.div);
                this.adaptive = true;
            }        
        } else {
            if(this.adaptive) {
                this.changePosDiv(this.parent, 'beforeend', this.div);
                this.adaptive = false;
            }
        }
    }

    changePosDiv(to, pos, div) {
        to.insertAdjacentElement(pos, div)
    }
}



const adaptiveBlock = document.querySelectorAll('.adaptive-block');
export const adaptiveDivArr = []
if(adaptiveBlock.length > 0) {
    adaptiveBlock.forEach(div => {
        const adaptiveDiv = new AdaptiveBlock(div);
        adaptiveDivArr.push(adaptiveDiv)
    })
}
