import { Accordion } from "./accordion.js";


 export class SearchCities {
    constructor(element) {
        this.element = element;
        this.title = this.element.querySelector('.cities-select__title');
        this.body = this.element.querySelector('.cities-select__body');
        this.searchInput = this.title.querySelector('input');
        this.citiesList = this.body.querySelectorAll('input');
        
        this.init(); 
        
    }
    
    init() {
        this.accordion = new Accordion(this.element);
        this._changeCite();
        this._searchCite();
    }


    _searchCite(){
        const empty = document.createElement('p');
        empty.classList.add('empty');
        empty.innerText = 'Ничего не найдено :('
        this.searchInput.addEventListener('input', (e) => {
            let findCities = [];
            const value = e.target.value.toLowerCase();

            this.citiesList.forEach(cite => {
                if(value.trim().length === 0 ) {
                    cite.parentElement.style.display = 'flex';
                    findCities.push(cite); 
                    return
                }
                if(cite.value.toLowerCase().includes(value.trim())) {
                    cite.parentElement.style.display = 'flex';
                    findCities.push(cite); 
                } else {
                    cite.parentElement.style.display = 'none';
                    findCities.filter(prev => prev !== cite);
                }
            })
            if(findCities.length === 0) {
                this.body.children[0]?.insertAdjacentElement('beforeend', empty);
            } else {
                empty.remove()
            }
            setTimeout(() => {
                this.accordion.openBody()
            }, 100)
        })
    }

    _changeCite() {
        this.citiesList.forEach(input => {
            if(input.checked) {
                this.searchInput.value = input.previousElementSibling.textContent;
            }
            input.addEventListener('input', () => {
                if(input.checked) {
                    this.searchInput.value = input.previousElementSibling.textContent;
                }
            })
        })
    }
}

const selectCities = document.querySelectorAll('.cities-select');
if(selectCities.length > 0) {
    selectCities.forEach(select => {
        new SearchCities(select)
    })
}