


export class CartSlider {
  constructor(element) {
    this.element = element;
    this._initSwiper();
  }

  _initSwiper() {
    this.swiper = new Swiper('.our-products-slider .swiper', {
        // Optional parameters
        direction: 'horizontal',
        effect: "fade",
        parallax: true,
        spaceBetween: 60,
        grabCursor: true,
      
        // Navigation arrows
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
    })
    this._slidesNumber()
  }
  
  _slidesNumber() {
    const slides = this.element.querySelectorAll('.swiper-slide');
    slides.forEach((slide, index) => {
      const numSpan = slide.querySelector('.swiper-slide__num');
      if(numSpan) {
        numSpan.innerHTML = index+1 + '/' + slides.length
      }
    });

  }
}


const ourProductSlider = document.querySelector('.our-products-slider .swiper');
if(ourProductSlider) {
  new CartSlider(ourProductSlider)
}
