



export class Accordion {
    constructor(accordion) {

        this.title = accordion.querySelector('.accordion-item__title') || accordion.querySelector('.acc-title');
        this.body = accordion.querySelector('.accordion-item__body') || accordion.querySelector('.acc-body');
        this.init()
    }

    init() {
        // this.accordionItems.forEach(item => {
            if(this.title.querySelector('input')) {
                const input = this.title.querySelector('input');
                const openBtn = this.title.querySelector('.icon');
                input.addEventListener('focus', () => {
                    this._openClickHandler();
                })
                openBtn.addEventListener('click', () => {
                    this._toggleClickHandler();
                })
                return;
            }
            this.title.addEventListener('click', () => {
                this._toggleClickHandler()
            })
        // });
    }
    
    _toggleClickHandler() {
        this.title.classList.toggle('active');
        this.body.classList.toggle('active');

        this.openBody();
    }

    _openClickHandler(title, body) {
        this.title.classList.add('active');
        this.body.classList.add('active');

        this.openBody();
    }

    openBody() {
        if(this.body.classList.contains('active')){
            this.body.style.height = this.body.children[0].clientHeight + 'px'
        } else {
            this.body.style.height = '0px';
        }
    }
}

const accordionItems = document.querySelectorAll('.accordion-item');
 
if(accordionItems.length > 0) {
    accordionItems.forEach(acc => {
        new Accordion(acc)
    })
}