const staticOffsetTop = () => {
    const posTop = window.pageYOffset || document.documentElement.scrollTop;
    setTimeout(() => document.documentElement.scrollTop = posTop, 320)
}

const setTemplateFrame = () => {
    const popupGet = document.querySelector('#popup-get');
    if(!popupGet) return; 
    const payForm = document.querySelector('#omForm-root') || '';
    const content = popupGet.querySelector('.modal__content');
 
 
    const prevClickBtn = () => {
        staticOffsetTop()
        setTimeout(() => {
            if(!payFormBtnNext) {
                payFormBtnNext = payForm.querySelector('.om-navigation__Next');
                payFormBtnPrev.addEventListener('click', () => {
                    prevClickBtn()
                })
            }
            if(!payForm.querySelector('.om-navigation__prev')) {
                payFormBtnPrev = null;
            }
        }, 340)
    }

    const nextClickBtn = () => {
        staticOffsetTop()
        setTimeout(() => {
            if(!payFormBtnPrev) {
                payFormBtnPrev = payForm.querySelector('.om-navigation__prev');
                payFormBtnPrev.addEventListener('click', () => {
                    prevClickBtn()
                })
            }
            if(!payForm.querySelector('.om-navigation__next')) {
                payFormBtnPrev = null;
            }
        }, 340)
    }


    content.insertAdjacentElement('beforeend', payForm);

    payForm.addEventListener('click', (e) => {
        
        if(e.target.closest('.om-navigation__prev') || e.target.closest('.om-navigation__next') || e.target.closest('.div-select__item')) {
            staticOffsetTop()
        }
    })    
    
    // let payFormBtnPrev = payForm.querySelector('.om-navigation__prev') || null;
    // let payFormBtnNext = payForm.querySelector('.om-navigation__next') || null;
    
    // if(payFormBtnPrev) {
    //     payFormBtnPrev.addEventListener('click', () => {
    //         prevClickBtn()
    //     })
    // }

    // if(payFormBtnNext) {
    //     payFormBtnNext.addEventListener('click', () => {
    //         nextClickBtn()
    //     })
    // }
}


document.addEventListener("DOMContentLoaded", function() {
    setTemplateFrame()
 })